.Users {
    overflow: scroll;
  overflow-x: auto;
}

.UsersTable {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  border: 1px solid #ddd;
}
.UsersTable > tbody > th {
  width: 25%;
}
.UsersTable > tbody > tr > th,
td {
  text-align: left;
  padding: 8px;
  width: 25%;
}
.UserDeleteIcon {
  width: 50px;
  height: 50px;
  opacity: 75%;
}
.UserDeleteIcon:hover {
  opacity: 100%;
}
.UserDeleteIcon:active {
  transform: translateY(4px);
}
.Button {
    display: inline-block;
    padding: 15px 25px;
    font-size: 24px;
    cursor: pointer;
    margin: 20px;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #6695e2;
    border: none;
    border-radius: 15px;
    box-shadow: 0 9px #999;
  }
  .Button:hover {
    background-color: #2f5ca5;
  }
  
  .Button:active {
    background-color: #2f5ca5;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
  }